import React, { Component } from 'react'
import PropTypes from 'prop-types'

import Grid from '@material-ui/core/Grid'
import Button from '@material-ui/core/Button'
import Dialog from '@material-ui/core/Dialog'
import Validation from '../../validations/user'
import CloseIcon from '@material-ui/icons/Close'
import TextField from '@material-ui/core/TextField'
import IconButton from '@material-ui/core/IconButton'
import Typography from '@material-ui/core/Typography'
import MuiDialogTitle from '@material-ui/core/DialogTitle'
import DialogContent from '@material-ui/core/DialogContent'
import DialogActions from '@material-ui/core/DialogActions'

import { withStyles } from '@material-ui/styles'


const styles = theme => ({
  dialogTitle: {
    margin: 0,
    padding: theme.spacing(2)
  },
  closeButton: {
    position: 'absolute',
    right: theme.spacing(1),
    top: theme.spacing(1),
    color: theme.palette.grey[500]
  },
  dialogActionsCancel: {
    marginRight: 'auto'
  },
  formControl: {
    marginTop: theme.spacing(1),
    minWidth: 120,
    width: '100%',
    '& .MuiInputLabel-outlined.MuiInputLabel-shrink': {
      background: theme.palette.common.white,
      paddingLeft: '5px',
      paddingRight: '5px'
    }
  }
})

class ProfileDialog extends Component {
  constructor(props) {
    super(props)

    this.state = {
      fieldData: {
        role: 'Admin',
        last_name: 'Doe',
        first_name: 'John',
        email: 'johndoe@agilewavetech.com',
        password: '',
        password2: ''
      },
      errors: {}
    }
  }

  validateInputs = () => {
    const { fieldData } = this.state
    const { isValid, errors } = Validation(fieldData, 'profile')

    if (isValid) {
        this.props.handleSave(fieldData)
    } else {
      this.setState({ errors })
    }

    return isValid
  }

  
  handleInputChanged = (e) => {
    const { id, value } = e.target
    this.setState({
        fieldData: {
          ...this.state.fieldData,
          [id]: value
        }
    })
  }

  render() {
    const { fieldData, errors } = this.state
    const { classes, open = false, handleClose } = this.props
    const { first_name, last_name, email, role, password, password2 } = fieldData
    return(
      <Dialog
          open={open}
          fullWidth
          aria-labelledby='simple-dialog-title'
        >
          <MuiDialogTitle disableTypography className={classes.dialogTitle}>
            <Typography variant='h6'>Edit Profile</Typography>
            <IconButton
              aria-label='close'
              className={classes.closeButton}
              onClick={() => handleClose(false)}
            >
              <CloseIcon />
            </IconButton>
          </MuiDialogTitle>
          <DialogContent dividers>
            <Grid container spacing={2}>
              <Grid item xs={6}>
                <TextField
                  required
                  fullWidth
                  id='first_name'
                  label='First Name'
                  value={first_name}
                  onChange={(e) => this.handleInputChanged(e)}
                  error={Boolean(errors && errors.first_name)}
                  helperText={errors ? errors.first_name : ''}
                />
              </Grid>
              <Grid item xs={6}>
                <TextField
                  required
                  fullWidth
                  id='last_name'
                  label='Last Name'
                  value={last_name}
                  onChange={(e) => this.handleInputChanged(e)}
                  error={Boolean(errors && errors.last_name)}
                  helperText={errors ? errors.last_name : ''}
                />
              </Grid>
            </Grid>
            <Grid container spacing={2}>
              <Grid item xs={6}>
                <TextField
                  required
                  fullWidth
                  id='email'
                  label='Email'
                  value={email}
                  error={Boolean(errors && errors.email)}
                  helperText={errors ? errors.email : ''}
                  onChange={(e) => this.handleInputChanged(e)}
                />
              </Grid>
              <Grid item xs={6}>
                <TextField
                  required
                  fullWidth
                  disabled
                  id='role'
                  label='Role'
                  value={role}
                  error={Boolean(errors && errors.role)}
                  helperText={errors ? errors.role : ''}
                  onChange={(e) => this.handleInputChanged(e)}
                />
              </Grid>
            </Grid>
            <Grid container spacing={2}>
              <Grid item xs={6}>
                <TextField
                  required
                  fullWidth
                  id='password'
                  label='Password'
                  type='password'
                  value={password}
                  error={Boolean(errors && errors.password)}
                  helperText={errors ? errors.password : ''}
                  onChange={(e) => this.handleInputChanged(e)}
                />
              </Grid>
              <Grid item xs={6}>
                <TextField
                  required
                  fullWidth
                  id='password2'
                  type='password'
                  value={password2}
                  label='Verify Password'
                  error={Boolean(errors && errors.password2)}
                  helperText={errors ? errors.password2 : ''}
                  onChange={(e) => this.handleInputChanged(e)}
                />
              </Grid>
            </Grid>
          </DialogContent>
          <DialogActions>
            <Button 
              color='default'
              variant='outlined'
              onClick={() => handleClose(false)}
              className={classes.dialogActionsCancel} >
              Cancel
            </Button>
            <Button onClick={() => this.validateInputs()} color='primary' variant='contained'>
              Save
            </Button>
          </DialogActions>
        </Dialog>
    )
  }
}

ProfileDialog.propTypes = {
  classes: PropTypes.object.isRequired
}

export default withStyles(styles)(ProfileDialog)