const INITIAL_STATE = {
  list: { data: [], count: 0 },
  data: null
}

const actionHandlers = {
  ORGANIZATION_LIST: (state, action) => {
    return {
      ...state,
      list: action.payload
    }
  },

  ORGANIZATION_GRID_SORT: (state, { payload }) => {
    return {
      ...state,
      list: {
        ...state.list,
        data: _.orderBy(state.list.data, [payload.key], [payload.direction])
      }
    }
  },
}

export default (state = INITIAL_STATE, action) => {
  if (actionHandlers.hasOwnProperty(action.type)) {
    return actionHandlers[action.type](state, action)
  }
  return state
}