import { put, takeLatest, call, all, select } from 'redux-saga/effects'
import { axios } from '../../utils/axios'
import uuid from 'uuidv4'
import _ from 'lodash'
import mockData from '../../utils/MockData'

import {
  ShowDialog
} from '../actions'

function *GetOrganizationList({ payload }) {
  const { search, sort_column, sort_direction, start, limit } = payload
  // const result = yield call(axios, { method: 'get' }, 'ORGANIZATION_LIST')
  const data = mockData.organization.data
  let newData = _.cloneDeep(data)
  if (search) {
    newData = newData.filter(({ id, ...rest }) => {
      const itemValues = Object.values(rest)
      return itemValues.find(val =>
        ['string', 'number'].includes(typeof val)
          && val.toString().toLowerCase().includes(search.toLowerCase())
      )
    })
  }

  if (sort_column && sort_direction) {
    newData = _.orderBy(newData, [sort_column], [sort_direction])
  }

  newData = newData.slice(start, limit)
  yield put({
    type: 'ORGANIZATION_LIST',
    payload: {
      data: newData,
      count:  search ? newData.length : data.length
    }
  })
}

function *GetOrganizationDetails({ payload }) {
  const details = mockData.organization.data.find(e => e.id === payload)
  yield all([
    put({
      type: 'UPDATE_BREADCRUMBS',
      payload: {
        type: 'organization',
        label: `${details.name}`,
        path: `/portal/organization/${details.id}`,
        entity: 'organization'
      }
    }),
    put({
      type: 'SET_ACTIVE_DATA',
      payload: details
    })
  ])
}

function *CreateOrganization({ payload }) {
  const { company, ...rest } = payload
  const { list } = yield select(state => state.user)

  yield all([
    put({
      type: 'UPDATE_ORGANIZATION',
      payload: [
        {
          id: uuid(),
          company: {
            name: company
          },
          ...rest
        },
        ...list
      ]
    }),
    put(ShowDialog(false))
  ])
}

function *UpdateOrganization({ payload }) {
  const { company, ...rest } = payload
  const { list } = yield select(state => state.user)

  yield all([
    put({
      type: 'UPDATE_ORGANIZATION',
      payload: list.map(e => {
        if (payload.id === e.id) {
          return {
            ...e,
            ...rest,
            company: {
              ...e.company,
              name: company
            }
          }
        }
        return e
      })
    }),
    put(ShowDialog(false))
  ])
}

function *DeleteOrganization({ payload }) {
  const { list } = yield select(state => state.user)

  yield all([
    put({
      type: 'UPDATE_ORGANIZATION',
      payload: list.filter(e => e.id !== payload)
    }),
    put(ShowDialog(false))
  ])
}

export default function *() {
  yield takeLatest('CREATE_ORGANIZATION_REQUESTED', CreateOrganization)
  yield takeLatest('UPDATE_ORGANIZATION_REQUESTED', UpdateOrganization)
  yield takeLatest('DELETE_ORGANIZATION_REQUESTED', DeleteOrganization)
  yield takeLatest('ORGANIZATION_LIST_REQUESTED', GetOrganizationList)
  yield takeLatest('ORGANIZATION_DETAILS_REQUESTED', GetOrganizationDetails)
}