import { put, takeLatest, call, all, select } from 'redux-saga/effects'
import { axios } from '../../utils/axios'
import uuid from 'uuidv4'
import _ from 'lodash'
import mockData from '../../utils/MockData'

import {
  ShowDialog
} from '../actions'

function *GetSurveyList({ payload }) {
  const { search, sort_column, sort_direction, start, limit } = payload
  // const result = yield call(axios, { method: 'get' }, 'SURVEY_LIST')
  const data = mockData.survey.data
  let newData = _.cloneDeep(data)
  if (search) {
    newData = newData.filter(({ id, ...rest }) => {
      const itemValues = Object.values(rest)
      return itemValues.find(val =>
        ['string', 'number'].includes(typeof val)
          && val.toString().toLowerCase().includes(search.toLowerCase())
      )
    })
  }

  if (sort_column && sort_direction) {
    newData = _.orderBy(newData, [sort_column], [sort_direction])
  }

  newData = newData.slice(start, limit)
  yield put({
    type: 'SURVEY_LIST',
    payload: {
      data: newData,
      count: search ? newData.length : data.length
    }
  })
}

function *GetSurveyDetails({ payload }) {
  const details = mockData.user.data.find(e => e.id === payload)
  yield put({
    type: 'UPDATE_BREADCRUMBS',
    payload: {
      details,
      type: 'user',
      label: `${details.first_name} ${details.last_name}`,
      path: `/user/${details.id}`
    }
  })
}

function *CreateSurvey({ payload }) {
  const { company, ...rest } = payload
  const { list } = yield select(state => state.user)

  yield all([
    put({
      type: 'UPDATE_SURVEY',
      payload: [
        {
          id: uuid(),
          company: {
            name: company
          },
          ...rest
        },
        ...list
      ]
    }),
    put(ShowDialog(false))
  ])
}

function *UpdateSurvey({ payload }) {
  const { company, ...rest } = payload
  const { list } = yield select(state => state.user)

  yield all([
    put({
      type: 'UPDATE_SURVEY',
      payload: list.map(e => {
        if (payload.id === e.id) {
          return {
            ...e,
            ...rest,
            company: {
              ...e.company,
              name: company
            }
          }
        }
        return e
      })
    }),
    put(ShowDialog(false))
  ])
}

function *DeleteSurvey({ payload }) {
  const { list } = yield select(state => state.user)

  yield all([
    put({
      type: 'UPDATE_SURVEY',
      payload: list.filter(e => e.id !== payload)
    }),
    put(ShowDialog(false))
  ])
}

export default function *() {
  yield takeLatest('CREATE_SURVEY_REQUESTED', CreateSurvey)
  yield takeLatest('UPDATE_SURVEY_REQUESTED', UpdateSurvey)
  yield takeLatest('DELETE_SURVEY_REQUESTED', DeleteSurvey)
  yield takeLatest('SURVEY_LIST_REQUESTED', GetSurveyList)
  yield takeLatest('SURVEY_DETAILS_REQUESTED', GetSurveyDetails)
}