export function GetOrganizationList(payload) {
  return {
    type: 'ORGANIZATION_LIST_REQUESTED',
    payload
  }
}

export function GetOrganizationDetails(payload) {
  return {
    type: 'ORGANIZATION_DETAILS_REQUESTED',
    payload
  }
}

export function CreateOrganization(payload) {
  return {
    type: 'CREATE_ORGANIZATION_REQUESTED',
    payload
  }
}

export function UpdateOrganization(payload) {
  return {
    type: 'UPDATE_ORGANIZATION_REQUESTED',
    payload
  }
}

export function DeleteOrganization(payload) {
  return {
    type: 'DELETE_ORGANIZATION_REQUESTED',
    payload
  }
}