import Validator from 'validator'

export default function (data, type) {
  let errors = {}
  
  
  if (Validator.isEmpty(data.email.trim())) {
    errors.email = 'Email is required.'
  }

  if (type === 'new' || type === 'profile') {
    if (Validator.isEmpty(data.first_name.trim())) {
      errors.first_name = 'First Name is required.'
    }
    if (Validator.isEmpty(data.last_name.trim())) {
      errors.last_name = 'Last Name is required.'
    }
    if (Validator.isEmpty(data.role.trim())) {
      errors.role = 'Role is required.'
    }
  }

  if (type === 'profile') {
    if (Validator.isEmpty(data.password.trim())) {
      errors.password = 'Password is required.'
    }
    if (Validator.isEmpty(data.password2.trim())) {
      errors.password2 = 'Password is required.'
    }
    if (!Validator.isEmpty(data.password.trim()) &&
      !Validator.isEmpty(data.password2.trim()) && (data.password !== data.password2)) {
      errors.password = 'Password did not match'
      errors.password2 = 'Password did not match.'
    }
  }

  if (type === 'contact') {
    if (Validator.isEmpty(data.subject.trim())) {
      errors.subject = 'Subject is required.'
    }
    if (Validator.isEmpty(data.inquiry_type.trim())) {
      errors.inquiry_type = 'Type of Inquiry is required.'
    }
    if (Validator.isEmpty(data.message.trim())) {
      errors.message = 'Message is required.'
    }
  }

  return {
    errors,
    isValid: Object.keys(errors).length === 0
  }
}