export function ShowDialog(payload = false) {
  return {
    type: 'SHOW_DIALOG',
    payload
  }
}

export function UpdateBreadcrumbs(payload) {
  return {
    type: 'UPDATE_BREADCRUMBS',
    payload
  }
}

export function ClearBreadcrumbs() {
  return {
    type: 'CLEAR_BREADCRUMBS'
  }
}

export function UpdateEntity(payload) {
  return {
    type: 'UPDATE_ENTITY',
    payload
  }
}

export function SetActiveData(payload) {
  return {
    type: 'SET_ACTIVE_DATA',
    payload
  }
}