import React, { PureComponent } from 'react'
import PropTypes from 'prop-types'

import { renderRoutes } from 'react-router-config'
import Routes from './Routes'
import routes from '../routes'
import NavItemLink from './NavItemLink'
import Logout from '../components/Dialogs/Logout'
import ProfileDialog from '../components/Dialogs/ProfileDialog'
import ContactDialog from '../components/Dialogs/ContactDialog'

// material UI
import { 
	List,
	Link,
	Menu,
	Icon,
	Button,
	Drawer,
	AppBar,
	Avatar,
	Toolbar,
	ListItem,
	MenuItem,
	Typography,
	IconButton,
	makeStyles,
	ListItemIcon,
	ListItemText,
	ListItemAvatar
} from '@material-ui/core'
import DeleteIcon from '@material-ui/icons/Delete'
import MoreVertIcon from '@material-ui/icons/MoreVert'
import { withStyles } from '@material-ui/styles'


const drawerWidth = 300;
const styles = theme => ({
	root: {
		display: 'flex',
		flexDirection: 'column',
		height: '100vh'
	},
	appBar: {
		zIndex: theme.zIndex.drawer + 1,
	},
	logo: {
		flex: 1,
		'& img': {
			maxWidth: '200px',
			marginTop: '5px'
		}
	},
	drawer: {
		width: drawerWidth,
		flexShrink: 0,
	},
	drawerPaper: {
		width: drawerWidth,
		boxShadow: '0 0 5px rgba(0, 0, 0, 0.12)'
	},
	inline: {
    display: 'inline',
	},
	userProfile: {
		marginBottom: '1rem',
		padding: '1rem 16px 1.5rem',
		borderBottom: `1px solid ${theme.palette.divider}`,
		'& .MuiListItemAvatar-root': {
			minWidth: '75px'
		},
		'& .MuiListItemText-primary': {
			fontWeight: 'bold'
		}
	},
	userProfileLarge: {
		width: theme.spacing(7),
		height: theme.spacing(7),
	},
	menuButton: {
		position: 'relative',
		right: '-15px'
	},
	content: {
		flexGrow: 1,
		padding: theme.spacing(3),
		marginLeft: '18.7rem'
	},
	toolbar: theme.mixins.toolbar,
	footer: {
		marginTop: 'auto',
		marginLeft: '16rem',
		padding: '1.5rem',
		borderTop: '1px solid #f3f3f3'
	}
})

function Copyright() {
  return (
    <Typography
      variant="body2"
      color="textSecondary"
      align="center"
    >
      {"Copyright © "}
      <Link color="inherit" href="#">
        Network Centrality
      </Link>{" "}
      {new Date().getFullYear()}
      {"."}
    </Typography>
  );
}

class App extends PureComponent {
	constructor(props) {
		super(props)

		this.state = {
			// initialize states here
			showMenu: false,
			showLogout: false,
			showProfile: false,
			showContact: false,
			anchorEl: null
		}
	}

	handleClickMenuOpen = (e, bool) => {
		this.setState({ 
			showMenu: bool,
			anchorEl: e.currentTarget
		})
	}

	
	
	handleClose = (bool) => {
		this.setState({
			showLogout: bool,
			showProfile: bool,
			showContact: bool
		})
	}

	handleShowDialog = (type, bool) => {
		if (type === 'profile') {
			this.setState({
				showProfile: bool,
				showMenu: false
			})
		} else if (type === 'contact') {
			this.setState({
				showContact: bool,
				showMenu: false
			})
		} else {
			this.setState({
				showMenu: false,
				showLogout: bool,
				showProfile: false,
				showContact: false
			})
		}
	}

	render() {
		const { classes } = this.props
		const { showLogout, showMenu, showProfile, showContact, anchorEl } = this.state
		return (
			<div className={classes.root}>
				<AppBar position="fixed" className={classes.appBar}>
					<Toolbar>
						<Typography variant="div" noWrap className={classes.logo}>
							{/* LOGO TITLE */}
							<img src='/imgs/logo/network_centrality_white.png' />
						</Typography>
						<Button color="inherit" id='logout' onClick={() => this.handleShowDialog('logout', true)}>Logout</Button>
					</Toolbar>
					<Logout
						open={showLogout}
						handleClose={() => this.handleClose(false)}
					/>
				</AppBar>
				<Drawer
					className={classes.drawer}
					variant="permanent"
					classes={{
						paper: classes.drawerPaper,
					}}
				>
					<div className={classes.toolbar} />
					<List>
						<ListItem alignItems='center' className={classes.userProfile}>
							<ListItemAvatar>
								<Avatar alt='Remy Sharp' src='/imgs/profile.png' className={classes.userProfileLarge}/>
							</ListItemAvatar>
							<ListItemText
								primary='Jhon Doe'
								secondary={
									<>
										<Typography
											variant='body2'
											component='span'
											color='textPrimary'
											className={classes.inline}
										>
											Admin
										</Typography>
									</>
								}
							/>
							<div className={classes.menu}>
								<IconButton
									aria-label='more'
									aria-haspopup='true'
									aria-controls='menu'
									className={classes.menuButton}
									onClick={(e) => this.handleClickMenuOpen(e, true)}
								>
									<MoreVertIcon />
								</IconButton>
								<Menu
									id='menu'
									keepMounted
									open={showMenu}
									anchorEl={anchorEl}
									PaperProps={{
										style: {
											maxHeight: 48 * 4.5,
											width: 150,
										},
									}}
								>
									<MenuItem onClick={() => this.handleShowDialog('profile', true)}>Profile</MenuItem>
									<MenuItem onClick={() => this.handleShowDialog('contact', true)}>Contact</MenuItem>
								</Menu>
								<ProfileDialog
									open={showProfile}
									handleClose={this.handleClose}
								/>
								<ContactDialog
									open={showContact}
									handleClose={this.handleClose}
								/>
							</div>
						</ListItem>
						{Routes.map(props => <NavItemLink {...props} key={props.to} />)}
					</List>
				</Drawer>
				<main className={classes.content}>
					<div className={classes.toolbar} />
					{renderRoutes(routes)}
				</main>
				<footer className={classes.footer}>
					<Copyright />
				</footer>
		</div>
		)
	}
}

App.propTypes = {
	classes: PropTypes.object.isRequired
}

export default withStyles(styles)(App)
