import { put, takeLatest, call, all, select } from 'redux-saga/effects'
import { axios } from '../../utils/axios'
import uuid from 'uuidv4'
import _ from 'lodash'
import mockData from '../../utils/MockData'

import {
  ShowDialog
} from '../actions'

function *GetUserList({ payload }) {
  const { search, sort_column, sort_direction, start, limit, entity, entity_id } = payload
  // const result = yield call(axios, { method: 'get' }, 'USER_LIST')
  const data = mockData.user.data
  let newData = _.cloneDeep(data)

  if (entity && entity_id) {
    newData = newData.filter((e) => e[`${entity}_id`] === entity_id)
  }

  if (search) {
    newData = newData.filter(({ id, ...rest }) => {
      const itemValues = Object.values(rest)
      return itemValues.find(val =>
        ['string', 'number'].includes(typeof val)
          && val.toString().toLowerCase().includes(search.toLowerCase())
      )
    })
  }

  if (sort_column && sort_direction) {
    newData = _.orderBy(newData, [sort_column], [sort_direction])
  }

  yield put({
    type: 'USER_LIST',
    payload: {
      data: newData.slice(start, limit),
      count: entity && entity_id || search ? newData.length : data.length
    }
  })
}

function *GetUserDetails({ payload }) {
  const details = mockData.user.data.find(e => e.id === payload)
  yield all([
    put({
      type: 'UPDATE_BREADCRUMBS',
      payload: {
        type: 'user',
        label: `${details.first_name} ${details.last_name}`,
        path: `/user/${details.id}`,
        entity: 'user'
      }
    }),
    put({
      type: 'SET_ACTIVE_DATA',
      payload: details
    })
  ])
}

function *CreateUser({ payload }) {
  const { company, ...rest } = payload
  const { list } = yield select(state => state.user)

  yield all([
    put({
      type: 'UPDATE_USER',
      payload: [
        {
          id: uuid(),
          company: {
            name: company
          },
          ...rest
        },
        ...list
      ]
    }),
    put(ShowDialog(false))
  ])
}

function *UpdateUser({ payload }) {
  const { company, ...rest } = payload
  const { list } = yield select(state => state.user)

  yield all([
    put({
      type: 'UPDATE_USER',
      payload: list.map(e => {
        if (payload.id === e.id) {
          return {
            ...e,
            ...rest,
            company: {
              ...e.company,
              name: company
            }
          }
        }
        return e
      })
    }),
    put(ShowDialog(false))
  ])
}

function *DeleteUser({ payload }) {
  const { list } = yield select(state => state.user)

  yield all([
    put({
      type: 'UPDATE_USER',
      payload: list.filter(e => e.id !== payload)
    }),
    put(ShowDialog(false))
  ])
}

export default function *() {
  yield takeLatest('CREATE_USER_REQUESTED', CreateUser)
  yield takeLatest('UPDATE_USER_REQUESTED', UpdateUser)
  yield takeLatest('DELETE_USER_REQUESTED', DeleteUser)
  yield takeLatest('USER_LIST_REQUESTED', GetUserList)
  yield takeLatest('USER_DETAILS_REQUESTED', GetUserDetails)
}