export function GetSurveyList(payload) {
  return {
    type: 'SURVEY_LIST_REQUESTED',
    payload
  }
}

export function GetSurveyDetails(payload) {
  return {
    type: 'SURVEY_DETAILS_REQUESTED',
    payload
  }
}

export function CreateSurvey(payload) {
  return {
    type: 'CREATE_SURVEY_REQUESTED',
    payload
  }
}

export function UpdateSurvey(payload) {
  return {
    type: 'UPDATE_SURVEY_REQUESTED',
    payload
  }
}

export function DeleteSurvey(payload) {
  return {
    type: 'DELETE_SURVEY_REQUESTED',
    payload
  }
}